import { getDictionary } from '@/dictionaries/get-dictionary';
import styles from './SolutionByDesign.module.css';
import Link from 'next/link';
import PageHero from '@/components/common/PageHero';

export default async function SolutionByDesignPage({
    params,
}: {
    params: Promise<{ locale: string }>;
}) {
    const { locale } = await params;
    const dict = await getDictionary(locale);
    const sbd = dict.solutionByDesign;

    return (
        <main className={styles.main}>
            <PageHero
                category={sbd.category}
                headline={sbd.headline}
                clarifier={sbd.subline}
                archetype="explain"
            />

            <section className={styles.phases}>
                <div className="container">
                    <h2 className={styles.sectionTitle}>{sbd.phasesTitle}</h2>
                    <div className={styles.phaseGrid}>
                        {sbd.phases.map((phase: { num: string; title: string; desc: string }, i: number) => (
                            <div key={i} className={styles.phaseCard}>
                                <span className={styles.phaseNum}>{phase.num}</span>
                                <h3 className={styles.phaseTitle}>{phase.title}</h3>
                                <p className={styles.phaseDesc}>{phase.desc}</p>
                            </div>
                        ))}
                    </div>
                </div>
            </section>

            <section className={styles.benefits}>
                <div className="container">
                    <h2 className={styles.sectionTitle}>{sbd.benefitsTitle}</h2>
                    <div className={styles.benefitGrid}>
                        {sbd.benefits.map((benefit: { icon: string; title: string; desc: string }, i: number) => (
                            <div key={i} className={styles.benefitCard}>
                                <div className={styles.benefitIcon}>
                                    {benefit.icon === 'speed' && (
                                        <svg width="32" height="32" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                                            <polygon points="13 2 3 14 12 14 11 22 21 10 12 10 13 2" />
                                        </svg>
                                    )}
                                    {benefit.icon === 'shield' && (
                                        <svg width="32" height="32" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                                            <path d="M12 22s8-4 8-10V5l-8-3-8 3v7c0 6 8 10 8 10z" />
                                            <polyline points="9 12 11 14 15 10" />
                                        </svg>
                                    )}
                                    {benefit.icon === 'eye' && (
                                        <svg width="32" height="32" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                                            <path d="M1 12s4-8 11-8 11 8 11 8-4 8-11 8-11-8-11-8z" />
                                            <circle cx="12" cy="12" r="3" />
                                        </svg>
                                    )}
                                    {benefit.icon === 'check' && (
                                        <svg width="32" height="32" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                                            <circle cx="12" cy="12" r="10" />
                                            <polyline points="9 12 11 14 15 10" />
                                        </svg>
                                    )}
                                    {benefit.icon === 'refresh' && (
                                        <svg width="32" height="32" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                                            <polyline points="23 4 23 10 17 10" />
                                            <polyline points="1 20 1 14 7 14" />
                                            <path d="M3.51 9a9 9 0 0 1 14.85-3.36L23 10M1 14l4.64 4.36A9 9 0 0 0 20.49 15" />
                                        </svg>
                                    )}
                                </div>
                                <h3 className={styles.benefitTitle}>{benefit.title}</h3>
                                <p className={styles.benefitDesc}>{benefit.desc}</p>
                            </div>
                        ))}
                    </div>
                </div>
            </section>

            <section className={styles.cta}>
                <div className="container">
                    <h2 className={styles.ctaTitle}>{sbd.ctaTitle}</h2>
                    <div className={styles.ctaActions}>
                        <Link href={`/${locale}/technology`} className={styles.primaryBtn}>
                            {sbd.ctaTech}
                        </Link>
                        <Link href={`/${locale}/contact`} className={styles.secondaryBtn}>
                            {sbd.ctaContact}
                        </Link>
                    </div>
                </div>
            </section>
        </main>
    );
}
